import React, {useState} from 'react'
import "./videos.css"
import Videos from "./videoframe"

export default function Vids(props){
    const [video, setVideo] = useState(["https://www.youtube.com/watch?v=FMC8GWT4xVA"]);
    const [vids, setNewVid] = useState("");
   
   
    const handleAddItem = (e) => {
    setVideo([...video, vids]);
    setNewVid("");
    console.log(video);
    };
    
    const handleInput = (e) => {
        setNewVid(e.target.value)
    }


return (
    <>
    <div className="addvideo">
        <input type="text" value={vids} onChange={handleInput} />
        <button onClick={handleAddItem}>Adicione url de um video</button>
    </div>
    <article className="wrapper">
        {video.map((v) => <Videos videoID={v} />)}
        </article>
        </>
    )
    }