import React from 'react'
import "./videos.css"
import { useState, useEffect } from "react";


export default props => {
    const [data, setVideo] = useState([])

useEffect(() => {
    fetch(`https://noembed.com/embed?url=https://${props.videoID}`).then(async (response) => {
        const data = await response.json();
        setVideo(data)
    })
}, [props.videoID])
    
      
    return (
        <div className="videoframes" >
            <div className="videoframe">
                <a href={data.url}><img src={data.thumbnail_url} alt="" />
                </a>
            </div>
            <div className="videotitle"><h4>{data.title}</h4></div>
        </div>
    )
}

    
    







